.navbar {
    position: fixed;
    top: 0;
    right: 0;
    padding: 15px;
    background-color: #0d071f;
    display: flex;
    justify-content: right;
    width: 100%;
    z-index: 1000;
  }
  
  .navbar a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
    margin: 0 15px;
    cursor: pointer;
  }
  
  .navbar a:hover {
    color: #007bff;
  }
  
  @media screen and (max-width: 768px) {
    .navbar {
      flex-direction: column; 
      align-items: flex-end; 
    }
  
    .navbar a {
      margin: 10px 0; 
      font-size: 1rem;
    }
  }