.contact-container {
    text-align: center;
  }
  
  .contact-container h2 {
    font-size: 2rem;
    margin-top: 50px;
    margin-bottom: 60px;
  }
  
  .contact-form {
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 100px;
  }
  
  label {
    font-size: 1.2rem;
    margin-bottom: 8px;
  }
  
  input,
  textarea {
    padding: 8px;
    margin-bottom: 16px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    margin: 0 auto;
    background-color: #007bff;
    color: #fff;
    width: 30%;
    padding: 10px 20px;
    font-size: 1.2rem;
    cursor: pointer;
    border: none;
    border-radius: 4px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  