.projects{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

.projects h2 {
  margin-top: 100px;
}

.projects p {
  font-size: 1.5rem;
  max-width: 50%;
  margin: auto;
  margin-bottom: 50px;
  }

.project-box {
    margin: 20px auto;
    padding: 10px;
    max-width: 50%;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
  
    img {
      max-width: 100%;
      height: auto;
      border-radius: 8px;
      margin-bottom: 10px;
      transition: transform 0.3s ease;
  
      &:hover {
        transform: scale(1.1);
      }
    }
  
    h3 {
      font-size: 1.5rem;
      margin-bottom: 30px;
    }
  
  
    a::after {
      content: attr(title);
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #007bff;
      color: #fff;
      padding: 15px;
      opacity: 0;
      transition: opacity 0.3s ease;
    }
  
    &:hover a::after {
      opacity: 1;
    }
}
  