.skills-container {
    text-align: center;
    margin: 0px auto;
  }
  
  .skills-container h2 {
    font-size: 2rem;
    margin-top: 100px;
    margin-bottom: 100px;
    color: azure;
  }
  
  .skill {
    width: 40%;
    margin: 0 auto;
    text-align: left;
    margin-bottom: 100px;
  }
  
  .skill-name {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .progress-bar {
    width: 100%;
    background-color: #e0e0e0;
    height: 25px;
    border-radius: 8px;
    overflow: hidden;
    position: relative;
  }
  
  .progress {
    height: 100%;
    background-color: #007bff;
    position: relative;
  }
  
  .percentage {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgb(0, 0, 0);
    z-index: 1;
  }
  